import React from "react";

export default function CartIcon({ cartLength, toggleShowCart, cartRef }) {
  return (
    <div className="shop-cart" ref={cartRef} onClick={toggleShowCart}>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        className="shopping-bag-icon"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
        />
      </svg>

      {cartLength ? (
        <label className="cart-module-label">{cartLength}</label>
      ) : (
        ""
      )}
    </div>
  );
}
